angular.module("meanbaseApp").run(["$templateCache", function($templateCache) {$templateCache.put("extensions/provision/index.html","<div class=\"col-lg-4 col-sm-6 text-center\">\n	<span single-image=\"{{extension.data.employeePhoto}}\" placehold-it=\"http://placehold.it/200x200\" image-classes=\"img-circle img-responsive img-center\"></span>\n  <h3 meanbase-editable ng-bind-html=\"extension.data.employee\">John Smith</h3>\n  <p meanbase-editable ng-bind-html=\"extension.data.description\">What does this team member to? Keep it short! This is also a great spot for social links!</p>\n</div>\n<div ng-if=\"editMode\">\n	<div class=\"form-group\">\n    <input type=\"text\" ng-model=\"extension.data.employeePhoto\" placeholder=\"Employee\" class=\"form-control\">\n  </div>\n</div>");}]);


